.s-partners-page {
    padding: 100px 0 110px;

    @media (max-width: 1699px) {
        padding: 85px 0 110px;
    }

    @media (max-width: 1199px) {
        padding: 60px 0 90px;
    }

    @media (max-width: 991px) {
        padding: 50px 0 80px;
    }

    @media (max-width: 600px) {
        padding: 48px 0 50px;
    }

    .title {
        text-align: center;
        margin-bottom: 82px;

        @media (max-width: 1699px) {
            margin-bottom: 70px;
        }

        @media (max-width: 1199px) {
            margin-bottom: 55px;
        }

        @media (max-width: 991px) {
            margin-bottom: 60px;
        }

        @media (max-width: 600px) {
            margin-bottom: 31px;
        }
    }

    &__items {
        display: flex;
        flex-wrap: wrap;

        @media (max-width: 1199px) {
            justify-content: center;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 20%;
        border-right: 1px solid #424346;

        @media (max-width: 1199px) {
            width: 33.3333%;
        }

        @media (max-width: 600px) {
            width: 50%;
        }

        &:after {
            @include tr('all');
            content: '';
            width: calc(100% - 53px);
            height: 1px;
            background: #424346;
            margin-top: 30px;

            @media (max-width: 1699px) {
                width: calc(100% - 48px);
                max-width: 160px;
                margin-top: 25px;
            }

            @media (max-width: 991px) {
                &:nth-child(3n) {
                    border-right: none;
                }
            }

            @media (max-width: 600px) {
                width: calc(100% - 37px);
                margin-top: 8px;
            }
        }

        &:nth-child(5n) {
            border-right: none;

            @media (max-width: 1199px) {
                border-right: 1px solid #424346;
            }
        }

        &:nth-child(3n) {
            @media (max-width: 1199px) {
                border-right: none;
            }

            @media (max-width: 600px) {
                border-right: 1px solid #424346;
            }
        }

        &:nth-child(2n) {
            @media (max-width: 600px) {
                border-right: none;
            }
        }

        &:last-child {
            @media (max-width: 1199px) {
                border-left: 1px solid #424346;
            }
        }

        &:nth-child(8) {
            &:after {
                background: $red !important;
            }

            .s-partners-page {
                &__item {
                    &-text {
                        color: #fff;
                    }
                }
            }
        }

        &-logo {
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: 158px;
            margin: 0 auto;

            @media (max-width: 1699px) {
                min-height: 131px;
            }

            @media (max-width: 600px) {
                min-height: 81px;
            }

            img {
                max-width: 170px;
                max-height: 85px;

                @media (max-width: 1699px) {
                    max-width: 141px;
                }

                @media (max-width: 991px) {
                    max-width: 126px;
                }

                @media (max-width: 600px) {
                    max-width: 100px;
                    max-height: 52px;
                }
            }
        }

        &:hover {
            img {
                filter: brightness(0) invert(1);
            }

            &:after {
                background: #CECED0;
            }
        }

        &:active {
            &:after {
                background: $red;
            }
        }

        &-text {
            @include tr('all');
            font-size: 17px;
            line-height: 120%;
            font-weight: 300;
            letter-spacing: 0.38em;
            color: #424346;

            @media (max-width: 1699px) {
                font-size: 15px;
            }

            @media (max-width: 991px) {
                font-size: 14px;
            }

            @media (max-width: 600px) {
                font-size: 10px;
            }
        }
    }

    &__btn {
        @include tr('all');

        &:hover {
            .s-partners-page {
                &__item {
                    &-text {
                        color: #fff;
                    }
                }
            }
        }

        &:active {
            .s-partners-page {
                &__item {
                    &-text {
                        color: $red;
                    }
                }
            }

            &:after {
                background: $red;
            }
        }
    }
}
